import React from "react";
import AppIcons from "Pub/js/appIcons";
import DefaultAppIcon from "Assets/images/default.svg"; // 默认图标
import { getStore } from "Pub/js/storageUtils";

const AppIcon = ({ appCode }) => {
    let AppIconStore = getStore("APPICONSSTORE");
    let iconName = AppIconStore && AppIconStore[appCode];
    let icon = AppIcons[iconName] ? AppIcons[iconName] : DefaultAppIcon;
    return (
        <div
            style={{
                width: "24px",
                height: "24px",
                marginRight: "10px",
                background: `url(${icon}) no-repeat 0px 0px`,
                backgroundSize: "contain"
            }}
        />
    );
};

export default AppIcon;
