import React from "react";
import { Table } from "antd";
import { DragSource, DropTarget } from "react-dnd";
import { Resizable } from "react-resizable";
import "./index.less";
import platform from "Pub/js/platform";
const { NCDiv } = platform.base;
// 可伸缩单元格
const ResizeableTitle = props => {
    const { onResize, width, ...restProps } = props;
    if (!width) {
        return <th {...restProps} />;
    }
    return (
        <Resizable width={width} height={0} onResize={onResize}>
            <th {...restProps} />
        </Resizable>
    );
};

function dragDirection(
    dragIndex,
    hoverIndex,
    initialClientOffset,
    clientOffset,
    sourceClientOffset
) {
    const hoverMiddleY = (initialClientOffset.y - sourceClientOffset.y) / 2;
    const hoverClientY = clientOffset.y - sourceClientOffset.y;
    if (dragIndex < hoverIndex && hoverClientY > hoverMiddleY) {
        return "downward";
    }
    if (dragIndex > hoverIndex && hoverClientY < hoverMiddleY) {
        return "upward";
    }
}
let BodyRow = props => {
    const {
        isOver,
        connectDragSource,
        connectDropTarget,
        moveRow,
        dragRow,
        clientOffset,
        sourceClientOffset,
        initialClientOffset,
        ...restProps
    } = props;
    const style = { ...restProps.style, cursor: "move" };

    let className = restProps.className;
    if (isOver && initialClientOffset) {
        const direction = dragDirection(
            dragRow.index,
            restProps.index,
            initialClientOffset,
            clientOffset,
            sourceClientOffset
        );
        if (direction === "downward") {
            className += " drop-over-downward";
        }
        if (direction === "upward") {
            className += " drop-over-upward";
        }
    }

    return connectDragSource(
        connectDropTarget(
            <tr {...restProps} className={className} style={style} />
        )
    );
};
const rowTarget = {
    drop(props, monitor) {
        const dragIndex = monitor.getItem().index;
        const hoverIndex = props.index;
        // Don't replace items with themselves
        if (dragIndex === hoverIndex) {
            return;
        }
        // Time to actually perform the action
        props.moveRow(dragIndex, hoverIndex);
        // Note: we're mutating the monitor item here!
        // Generally it's better to avoid mutations,
        // but it's good here for the sake of performance
        // to avoid expensive index searches.
        monitor.getItem().index = hoverIndex;
    }
};
const rowSource = {
    beginDrag(props) {
        return {
            index: props.index
        };
    }
};
BodyRow = DropTarget("row", rowTarget, (connect, monitor) => ({
    connectDropTarget: connect.dropTarget(),
    isOver: monitor.isOver(),
    sourceClientOffset: monitor.getSourceClientOffset()
}))(
    DragSource("row", rowSource, (connect, monitor) => ({
        connectDragSource: connect.dragSource(),
        dragRow: monitor.getItem(),
        clientOffset: monitor.getClientOffset(),
        initialClientOffset: monitor.getInitialClientOffset()
    }))(BodyRow)
);
/**
 * 可伸缩列表格 每列必须固定宽度
 */
class TableResizableColumn extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            columns: this.props.columns
        };
        this.components = {
            header: {
                cell: ResizeableTitle
            }
        };
        // 当传入props中 onRow属性中有moveRow 事件时 添加行拖拽功能
        if (props.onRow&&props.onRow().moveRow) {
            this.components.body = {
                row: BodyRow
            };
        }
    }

    handleResize = index => (e, { size }) => {
        this.setState(({ columns }) => {
            const nextColumns = [...columns];
            nextColumns[index] = {
                ...nextColumns[index],
                width: size.width
            };
            return { columns: nextColumns };
        });
    };
    render() {
        let { columns } = this.state;
        const ResizeableColumns = columns.map((col, index) => {
            return {
                ...col,
                onHeaderCell: column => ({
                    width: column.width,
                    onResize: this.handleResize(index)
                })
            };
        });
        return (
            <NCDiv className="table_resizable_column" areaCode={NCDiv.config.TableCom} fieldid={this.props.fieldid}>
                <Table
                    bordered={true}
                    {...this.props}
                    components={this.components}
                    columns={ResizeableColumns}
                />
            </NCDiv>
        );
    }
}
export default TableResizableColumn;
