import React from "react";
import "./index.less";
/**
 * 吸顶表格单页布局
 */
const TableScroll = ({ children }) => {
    return (
        <div className="suck-table" id="suckTableHeader">
            <div className="suck-table-container">{children}</div>
        </div>
    );
};
export default TableScroll;
